body,
html {
  width: 100%;
  height: 100%;
}

body {
  font-family: 'Cabin', sans-serif;
}

.footer {
  background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%), url(/react-portfolio/static/media/portfolio-header.3627dca9.png);
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%), url(/react-portfolio/static/media/portfolio-header.3627dca9.png);
  padding-top: 5rem;
  padding-bottom: 5rem;
}
.content-section {
  padding-top: 7.5rem;
  padding-bottom: 7.5rem;
}

.content-section-heading h2 {
  font-size: 3rem;
}

.content-section-heading h3 {
  font-size: 1rem;
  text-transform: uppercase;
}

.skills {
  background-color: #f4763c;
}
.masthead {
  min-height: 30rem;
  position: relative;
  display: table;
  width: 100%;
  height: auto;
  padding-top: 8rem;
  padding-bottom: 8rem;
  background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%), url(/react-portfolio/static/media/portfolio-header.3627dca9.png);
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%), url(/react-portfolio/static/media/portfolio-header.3627dca9.png);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.masthead h1 {
  font-size: 4rem;
  margin: 0;
  padding: 0;
}

@media (min-width: 992px) {
  .masthead {
    height: 100vh;
  }
  .masthead h1 {
    font-size: 5.5rem;
  }
}
/* Side Menu */
.sidebar-enter {
  position: fixed;
  right: 0;
  width: 250px;
  height: 100%;
  z-index: 2;
  -webkit-transform: translateX(250px);
          transform: translateX(250px);
}

.sidebar-enter-active {
  -webkit-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.sidebar-enter-done {
  position: fixed;
  right: 0;
  width: 250px;
  height: 100%;
  z-index: 2;
}

.sidebar-exit {
  position: fixed;
  right: 0;
  width: 250px;
  height: 100%;
  z-index: 2;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.sidebar-exit-active {
  -webkit-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
  -webkit-transform: translateX(250px);
          transform: translateX(250px);
}

#sidebar-wrapper {
  background: #313455;
  border-left: 1px solid rgba(255, 255, 255, 0.1);
}

.sidebar-nav {
  position: absolute;
  top: 0;
  width: 250px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.sidebar-nav li.sidebar-nav-item a {
  display: block;
  text-decoration: none;
  color: #fff;
  padding: 15px;
}

.sidebar-nav li.sidebar-nav-item a:hover {
  text-decoration: none;
  color: #fff;
  background: rgba(255, 255, 255, 0.2);
}

.sidebar-nav li.sidebar-nav-item a:active,
.sidebar-nav li.sidebar-nav-item a:focus {
  text-decoration: none;
}

.menu-toggle {
  position: fixed;
  right: 15px;
  top: 15px;
  width: 50px;
  height: 50px;
  font-size: 1.1rem;
  text-align: center;
  color: #fff;
  background: rgba(52, 58, 64, 0.5);
  line-height: 15px;
  z-index: 999;
  border: none;
  box-shadow: none;
  outline: none;
}

.menu-toggle:focus, .menu-toggle:hover {
  color: #fff;
  border-radius: 0;
  box-shadow: none;
  outline: none;
}

.menu-toggle:hover {
  background: #343a40;
}
.btn-outline-light {
  margin: auto 1.5rem auto auto;
}

.btn-outline-light:focus {
  color: #fff;
}

.btn:hover:focus {
  color: #212529;
}

.btn-box {
  height: 3.7rem;
}

.portfolio-item {
  display: block;
  position: relative;
  overflow: hidden;
  max-width: 530px;
  margin: auto auto 1rem;
}

.portfolio-item .caption {
  display: flex;
  height: 100%;
  width: 100%;
  background-color: rgba(33, 37, 41, 0.6);
  position: absolute;
  top: 0;
  bottom: 0;
  z-index: 1;
}

.portfolio-item .caption .caption-content {
  color: #fff;
  margin: auto 2rem 2rem;
}

.portfolio-item .caption .caption-content h2 {
  font-size: 0.8rem;
  text-transform: uppercase;
}

.portfolio-item .caption .caption-content p {
  font-weight: 300;
  font-size: 1.2rem;
}

@media (min-width: 992px) {
  .portfolio-item {
    max-width: none;
    margin: 0;
  }
  .portfolio-item .caption {
    -webkit-transition: -webkit-clip-path 0.25s ease-out, background-color 0.7s;
    -webkit-clip-path: inset(0px);
    clip-path: inset(0px);
  }
  .portfolio-item .caption .caption-content {
    -webkit-transition: opacity 0.25s;
    transition: opacity 0.25s;
    margin-left: 5rem;
    margin-right: 5rem;
    margin-bottom: 5rem;
  }
  .portfolio-item img {
    -webkit-transition: -webkit-clip-path 0.25s ease-out;
    -webkit-clip-path: inset(-1px);
    clip-path: inset(-1px);
  }
  .portfolio-item:hover img {
    -webkit-clip-path: inset(2rem);
    clip-path: inset(2rem);
  }
  .portfolio-item:hover .caption {
    background-color: rgba(49, 52, 85, 0.9);
    -webkit-clip-path: inset(2rem);
    clip-path: inset(2rem);
  }
}

@media (max-width: 992px) {
  .portfolio-item .caption .caption-content h2 {
    line-height: 0.8rem;
  }
  
  .portfolio-item .caption .caption-content p {
    line-height: 1.2rem;
  }
}
.skill-icon {
  background-color: #fff;
  color: #f4763c;
  height: 7rem;
  width: 7rem;
  display: block;
  line-height: 7rem;
  font-size: 3rem;
  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.1);
}

.social-link {
  display: block;
  height: 4rem;
  width: 4rem;
  line-height: 3.7rem;
  font-size: 1.5rem;
  background-color: #e0487b;
  margin: 2.5rem 0.2rem 1.9rem 0.2rem;
  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.1);
}

.social-link:hover {
  background-color: #9e3560;
  text-decoration: none;
}

a {
  color: #e0487b;
}

a:hover, a:focus, a:active {
  color: #9e3560;
}

.headshot {
  margin: 0 auto 2rem auto;
  box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.1);
}
.btn-primary {
  background-color: #e0487b !important;
  border-color: #e0487b !important;
  color: #fff !important;
}

.btn-primary:hover, .btn-primary:focus, .btn-primary:active {
  background-color: #9e3560 !important;
  border-color: #9e3560 !important;
}

.btn-dark {
  background-color: #54333e;
  color: #fff !important;
}

.btn-dark:hover, .btn-dark:focus, .btn-dark:active {
  background-color: #24171e;
}

.btn-light {
  background-color: #fff;
  color: #f4763c !important;
}

.btn {
  box-shadow: 0px 3px 3px 0px rgba(0, 0, 0, 0.1);
  font-weight: 700;
}

.btn-xl {
  padding: 1.25rem 2.5rem;
}
.btnFade-enter {
  opacity: 0.01;
}

.btnFade-enter-active {
  opacity: 1;
  -webkit-transition: all 0.4s ease-in-out 0s;
  transition: all 0.4s ease-in-out 0s;
}

.btnFade-exit {
  opacity: 1;
}

.btnFade-exit-active {
  opacity: 0;
  -webkit-transition: all 0.4s ease-in-out 0s;
  transition: all 0.4s ease-in-out 0s;
}

.btnFade-exit-done {
  display: none;
}

.scroll-to-top {
  position: fixed;
  right: 15px;
  bottom: 15px;
  width: 50px;
  height: 50px;
  font-size: 1.1rem;
  text-align: center;
  color: white;
  background: rgba(52, 58, 64, 0.5);
  line-height: 15px;
  border: none;
  box-shadow: none;
  outline: none;
}

.scroll-to-top:focus, .scroll-to-top:hover {
  color: white;
  border-radius: 0;
  box-shadow: none;
  outline: none;
}

.scroll-to-top:hover {
  background: #343a40;
}
